// Not from npm!
function leftPad(str: string, pad: string, length: number): string {
    if (pad.length == 0) return str;

    while (str.length < length) {
        str = pad + str;
    }

    return str;
}

export const ACCESSORIES: Accessory[] = [
    ...EYES_ACCESSORIES,
    ...HEAD_ACCESSORIES,
    ...MOUTH_ACCESSORIES,
    ...HAIR_ACCESSORIES,
    ...BODY_ACCESSORIES
];
export default ACCESSORIES;
