
import { bindActionCreators } from "redux";

import { Dispatch } from "../../../../../../../../state/utils";

import { addDuplicantAccessory, removeDuplicantAccessory} from "../../../../../../../../services/save-editor/duplicants/actions";

export interface DispatchProps {
    addAccessory: typeof addDuplicantAccessory;
    removeAccessory: typeof removeDuplicantAccessory;
}

export default function mapDispatchToProps(dispatch: Dispatch): DispatchProps {
    return bindActionCreators({
        addAccessory: addDuplicantAccessory,
        removeAccessory: removeDuplicantAccessory,
    }, dispatch);
}