
import * as React from "react";
import { connect } from "react-redux";
import { autobind } from "core-decorators";

import { Button, MenuItem } from "@blueprintjs/core";
import { IconNames } from "@blueprintjs/icons";
import { Select, IItemRendererProps } from "@blueprintjs/select";

import { Accessory } from "../../../../../../../../services/save-editor/behaviors";

import DuplicantAccessoryPageProps from "./props";
import mapStateToProps, { StateProps } from "./selectors";
import mapDispatchToProps, { DispatchProps } from "./dispatch";

import ACCESSORIES from "./accessories";

const AccessorySelect = Select.ofType<Accessory>();

type Props = DuplicantAccessoryPageProps & StateProps & DispatchProps;
class DuplicantAccessoryPage extends React.Component<Props> {
    render() {
        const {
            accessories
        } = this.props;

        const rows = accessories.map((x, i) => (
            <RemovableRow key={i} id={x.guid.Guid} onRemove={this._onRemoveAccessory}/>
        ));

        return (
            <div className={`ui-duplicant-accessories fill-parent layout-vertical`}>
                <div className="container-scroll layout-item-fill">
                    <table className="pt-html-table pt-html-table-striped fill-parent-width">
                        <thead>
                            <tr>
                                <th>Accessory</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {rows}
                        </tbody>
                    </table>
                </div>
                <div>
                    <AccessorySelect
                        items={ACCESSORIES}
                        itemPredicate={this._filterItem}
                        itemRenderer={this._renderItem}
                        onItemSelect={this._onAccessorySelected}
                        filterable={true}
                        resetOnClose={true}
                        resetOnSelect={true}
                        popoverProps={{ minimal: true }}
                    >
                        <Button rightIcon="caret-down" text="Add a new Accessory" />
                    </AccessorySelect>
                </div>
            </div>
        );
    }

    private _filterItem(query: string, accessory: Accessory) {
        return accessory.guid.Guid.toLowerCase().indexOf(query.toLowerCase()) !== -1;
    }

    @autobind()
    private _renderItem(accessory: Accessory, itemProps: IItemRendererProps) {
        const {
            modifiers,
            handleClick
        } = itemProps;

        if (!modifiers.matchesPredicate) {
            return null;
        }

        const name = accessory.guid.Guid;

        return (
            <MenuItem
                active={modifiers.active}
                key={name}
                onClick={handleClick}
                text={name}
            />
        );
    };

    @autobind()
    private _onAccessorySelected(accessory: Accessory) {
        const {
            duplicantID,
            addAccessory
        } = this.props;
        addAccessory({ duplicantID, accessoryID: accessory.guid.Guid});
    }

    @autobind()
    private _onRemoveAccessory(accessoryID: string) {
        const {
            duplicantID,
            removeAccessory
        } = this.props;
        removeAccessory({duplicantID, accessoryID});
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(DuplicantAccessoryPage);


interface RemovableRowProps {
    id: string;
    onRemove(id: string): void
}
class RemovableRow extends React.Component<RemovableRowProps> {
    render() {
        const {
            id
        } = this.props;
        return <tr>
            <td>{id}</td>
            <td><Button icon={IconNames.SMALL_CROSS} onClick={this._onRemove}/></td>
        </tr>
    }

    @autobind()
    private _onRemove() {
        const {
            id,
            onRemove
        } = this.props;
        onRemove(id);
    }
}